@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;500;600;700&display=swap');

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Kumbh Sans', sans-serif;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.main{
  width: 100%;
  height: 100%;
  background-color: white;
  padding: 20px 100px;
  overflow: hidden;
}

.nav_main{
  width: 100%;
  height: 100px;
  border-bottom: 2px solid hsl(220, 14%, 75%);
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.nav_left{
  display: flex;
  align-items: center;
  height: 100px;

}

.nav_right{
  display: flex;
  align-items: center;
  height: 100px;

}

.menu_list{
  display: flex;
  list-style: none;
  margin: 0 50px;
  width: 500px;
  height: 30px;
  justify-content: space-between;
}

.menu_list >li{
  color: hsl(219, 9%, 45%);
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}

.menu_list >li:hover{
  color: black;
  border-bottom: 3px solid hsl(26, 100%, 55%);
}

.avatar{
  width: 50px;
  margin: 0 50px;
}

.cart{
  cursor: pointer;
}

.cart:hover{
  color: black;
}


/* -----------------------PRODUCT MAIN-------------------------------- */


.product_main{
  width: 100%;
  /* border: 1px solid black; */
  height: 80%;
  display: flex;
}

.productLeft, .productRight{
  width: 50%;
  /* border: 1px solid black; */
  height: 100%;
  padding: 70px;
}

.productLeft{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
}

.main_img{
  width: 400px;
  height: 400px;
  border-radius: 15px;
}

.product_array{
  display: flex;
  justify-content: space-between;
  width: 400px;
  margin-top: 20px;
}

.thum_img{
  width: 70px;
  border-radius: 10px;
  cursor: pointer;
}

.imgActive{
  border: 5px solid hsl(26, 100%, 55%);
   transform: scale(0.8);

}


/* ------------------------------Product right-------------------------------- */

.productRight > h4{
   color:hsl(26, 100%, 55%) ;
   text-transform: uppercase;
   padding-top: 50px ;
   letter-spacing: 1.5px;
}

.productRight > h1{
  font-size: 45px;
  padding: 30px 0;
  font-weight: 700;
}


.productRight >p{
  color: hsla(0, 0%, 0%, 0.362);
  font-weight: 500;
  width: 550px;
}

.quantCart{
  padding: 20px;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.carticon{
  margin-right: 10px;
  font-weight: 600;
  font-size: 16px;
}

.cartBtn {
  padding: 12px 60px;
  border-radius: 9px;
  background: hsl(26, 100%, 55%);
  border: none;
  font-family: inherit;
  text-align: center;
  cursor: pointer;
  transition: 0.4s;
  color: white;
  font-weight: 600;
  font-size: 14px;
 }
 
 .cartBtn:hover {
  box-shadow: 7px 5px 56px -14px hsl(26, 100%, 55%);
 }
 
 .cartBtn:active {
  transform: scale(0.97);
  box-shadow: 7px 5px 56px -10px hsl(26, 100%, 55%);
 }

 .quant_main{
  background-color: hsl(231, 87%, 97%);
  border-radius: 5px;
 }

 .quantbtn{
  width: 40px;
  height: 40px;
  color: hsl(26, 100%, 55%);
  font-size: 30px;
  font-weight: 700;
  background-color: transparent;
  border: none;
  cursor: pointer;
 }

 .quantinp{
  width: 40px;
  height: 40px;
  color: hsla(0, 0%, 0%, 0.7);
  font-size: 18px;
  font-weight: 700;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  text-align: center;
 }
 .price_main {
  margin: 20px 0;
 }

 .price_main > h4{
  color: hsl(220, 14%, 75%);
  text-decoration: line-through;
}

.price_main > h1{
  display: inline;
  font-size: 2rem;
 }

 .price_main >span{
  color: hsl(26, 100%, 55%);
  font-weight: 700;
  font-size: 15px;
  padding: 1.5px 5px;
  border-radius: 3px;
  margin-left: 20px;
  background-color: hsl(25, 100%, 94%);
 }

 .cart_main{
  position: relative;
 }

 .sub_cart{
  width: 300px;
  height: 170px;
  position: absolute;
  background-color: white;
  border-radius: 10px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
  bottom: -180px;
  left: -150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
 }

 .sub_cart > h5{
  width: 100%;
  padding: 10px 20px;
  border-bottom: 2px solid hsla(220, 14%, 75%, 0.441);
 }

 .cp{
  width: 100%;
  height: 130px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

 }

 .cp_main{
  width: 280px;
  height: 50px;
  display: flex;
  align-items: center;
  margin: 10px;
 }

 .cartimg{
  width: 50px;
  border-radius: 5px;
  margin-right: 10px;
 }

 .cp_main >div > p{
  font-size: 14px;
  font-weight: 500;
  color: hsl(221, 11%, 66%);
 }


 /* --------------------------------------------------- */

 .cartbtn {
  position: relative;
  display: block;
  width: 28px;
  height: 28px;
  height: auto;
  overflow: hidden;
 }

  .count {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    font-size: 11px;
    border-radius: 50%;
    background: #d60b28;
    width: 14px;
    height: 14px;
    line-height:16px;
    display: block;
    text-align: center;
    color: white;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
  }
